@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
}

.buttonpane{
  background-color: hsl(225, 6%, 25%);
  font-family: sans-serif;
  text-align: center;
  box-sizing: border-box;
}
.buttonpane button {
  margin: 20px;
  
}

.author{
  font-family: "Roboto", sans-serif;
  text-align: center;
  font-weight: 400;
  background-color: hsl(225, 6%, 25%);
  color: rgb(255, 255, 255);
  box-sizing: border-box;
}

.top-pane {
  background-color: hsl(225, 6%, 25%);
}

.pane {
  height: 50vh;
  display: flex;
}

.editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: .5rem;
  background-color: hsl(225, 6%, 25%);
}

.editor-container.collapsed {
  flex-grow: 0;
}

.editor-container.collapsed .CodeMirror-scroll {
  position: absolute;
  overflow: hidden !important;
}

.expand-collapse-btn {
  margin-left: .5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.editor-title {
  display: flex;
  font-family: sans-serif;
  justify-content: space-between;
  background-color: hsl(225, 6%, 13%);
  color: white;
  padding: .5rem .5rem .5rem 1rem;
  border-top-right-radius: .5rem;
  border-top-left-radius: .5rem;
}

.CodeMirror {
  height: 100% !important;
}

.code-mirror-wrapper {
  flex-grow: 1;
  border-bottom-right-radius: .5rem;
  border-bottom-left-radius: .5rem;
  overflow: hidden;
}

:root {
    --primary: #39A9DB;
    --warning: #F9DC5C;
    --danger: #E84855;
    --success: #ABE188;
    --white: #fdfdfd;
    --dark: #181717;
  }
  
  .btn {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    border-radius: 10px;
    cursor: pointer;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  }
  
  .btn:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
  }
  
  /* Button Colors and Styles */
  
  .btn--primary--solid {
    background-color: #39A9DB;
    background-color: var(--primary);
    color: #fdfdfd;
    color: var(--white);
    border: none;
  }
  
  .btn--warning--solid {
    background-color: #F9DC5C;
    background-color: var(--warning);
    color: #181717;
    color: var(--dark);
    border: none;
  }
  
  .btn--danger--solid {
    background-color: #E84855;
    background-color: var(--danger);
    color: #fdfdfd;
    color: var(--white);
    border: none;
  }
  
  .btn--success--solid {
    background-color: #ABE188;
    background-color: var(--success);
    color: #fdfdfd;
    color: var(--white);
    border: none;
  }
  
  .btn--primary--outline {
    background-color: transparent;
    border: 2px solid #39A9DB;
    border: 2px solid var(--primary);
    color: #39A9DB;
    color: var(--primary);
  }
  
  .btn--warning--outline {
    background-color: transparent;
    border: 2px solid #F9DC5C;
    border: 2px solid var(--warning);
    color: #F9DC5C;
    color: var(--warning);
  }
  
  .btn--danger--outline {
    background-color: transparent;
    border: 2px solid #E84855;
    border: 2px solid var(--danger);
    color: #E84855;
    color: var(--danger);
  }
  
  .btn--success--outline {
    background-color: transparent;
    border: 2px solid #ABE188;
    border: 2px solid var(--success);
    color: #ABE188;
    color: var(--success);
  }
  
  /* Button Size */
  
  .btn--medium {
    padding: 10px 20px;
    font-size: 18px;
  }
  
  .btn--large {
    padding: 15px 30px;
    font-size: 20px;
  }
  
